function getPhone() {
  // 创建一个正则表达式
  var reg = new RegExp(/\d*$/);
  // window.location.hash 返回从 “#” 开始的 url
  // 返回一个数组（未匹配到则返回 null）
  var phone = reg.exec(window.location.hash)[0];
  return phone;
}

